@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  width: -webkit-max-content;
  width: max-content;
  margin: 1rem auto;
}

.typing-area {
  background-color: hsl(220, 30%, 40%);
  border: 0.5rem solid hsl(220, 30%, 20%);
  min-height: 10rem;
  padding: 1rem;
  font-size: 1.6rem;
  color: hsl(220, 50%, 90%);
  border-radius: 0.25rem;
}

._keyboard-module__container__279C2 {
  --main-hue: 220;
  --radius: 0.25rem;

  --color-primary: hsl(var(--main-hue), 65%, 50%);

  --color-grey-darker: hsl(var(--main-hue), 30%, 20%);
  --color-grey-dark: hsl(var(--main-hue), 30%, 40%);
  --color-grey: hsl(var(--main-hue), 30%, 50%);
  --color-grey-light: hsl(var(--main-hue), 30%, 90%);
}

._keyboard-module__container__279C2 * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

._keyboard-module__container__279C2 {
  background-color: var(--color-grey-darker);
  display: grid;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 1rem auto;
  padding: 0.5rem;
  grid-template-columns: repeat(30, 2rem);
  grid-template-rows: repeat(5, 4rem);
  grid-gap: 0.5rem;
  gap: 0.5rem;
  border-radius: var(--radius);
}

._keyboard-module__key__2DS1P {
  /*
    REMOVE DEFAULT STYLES
  */
  outline: none;
  border: none;

  grid-column-end: span 2;

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  font-weight: 600;

  background-image: linear-gradient(
    to bottom,
    var(--color-grey-dark),
    var(--color-grey)
  );
  color: var(--color-grey-light);

  border-radius: var(--radius);
  box-shadow: 0 4px 0 var(--color-grey-dark);

  transition: all 100ms;
  cursor: pointer;
}

._keyboard-module__key__2DS1P:hover,
._keyboard-module__key--active__2X4RO {
  transform: translateY(2px);
  box-shadow: 0 2px 0 var(--color-grey-dark);
}

._keyboard-module__key__2DS1P:active {
  transform: translateY(4px);
  box-shadow: 0 0 0 transparent;
}

._keyboard-module__key--span-2__3Vt12 svg,
._keyboard-module__key--shift__gWc3i svg,
._keyboard-module__key--space__1hUi5 svg {
  width: 2.6rem;
  height: 2.6rem;
}

._keyboard-module__key--span-2__3Vt12 {
  grid-column-end: span 4;
}

._keyboard-module__key--shift__gWc3i {
  grid-column-end: span 5;
}

._keyboard-module__key--space__1hUi5 {
  grid-column: 5 / span 20;
}

._keyboard-module__key--caps__2x_er {
  position: relative;
}

._keyboard-module__key--caps__badge__1OCsA {
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  top: 0.5rem;
  right: 0.5rem;
  border-radius: 50%;
  background-color: var(--color-grey-darker);
}

._keyboard-module__key--caps__badge--active__3wcLn {
  background-color: var(--color-grey-light);
}

